  margin: 0 auto;
  padding: 0 10px;
}

:root {
  --body-color: #fdf3e7;
  --mainBtns-color: #18191f;
  --activeMainBtns: #e5e5e5;
  --sidebar-width: 240px;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
